@import url("https://fonts.googleapis.com/css?family=Montserrat:400,800");

* {
  box-sizing: border-box;
}

.login-body {
  display: flex;
  position: relative;
  
  justify-content: center;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  height: 100vh;

  min-height: 100vh;
  background-image: url("../images/hinhnen.png");
  background-repeat: no-repeat;
  background-size:cover;
}

.button-login {
  border-radius: 20px;
  border: 1px solid #ffa2b0;
  background-color: #ffa2b0;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
  cursor: pointer;
}

.button-otp {
  border-radius: 20px;
  border: 1px solid #ffa2b0;
  background-color: #f3595b;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 64px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
  cursor: pointer;
}

.button-resent-otp {
  border-radius: 20px;
  border: 1px solid #f3595b;
  background-color: #ffffff;
  color: #f3595b;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 40px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
  cursor: pointer;
  margin-bottom: 30px;
}

.resent-opt-ctn > p {
  margin: 50px 0 10px;
}
.btn-popt-back {
  position: absolute;
  top: 20px;
  left: 20px;
  font-size: 13px;
  padding: 10opx;
  cursor: pointer;
}
.icon-back-login {
}

.button-login:active {
  transform: scale(0.95);
}

.button-login:focus {
  outline: none;
}

.btn-ghost {
  background-color: transparent;
  border-color: #ffffff;
  position: absolute;
  bottom: 60px;
}

.form-login {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.form-login .head {
  padding-bottom: 30px;
  font-weight: bold;
  margin: 0;
  font-size: 30px;
}
.hear-welcome {
  padding-bottom: 30px;
  font-weight: 600;
  margin: 0;
  font-size: 30px;
}
.hear-welcome-light {
  padding-bottom: 30px;
  font-weight: 500;
  margin: 0;
  font-size: 30px;
}
.sub-he {
  font-size: 15px;
  padding-bottom: 20px;
}
.input-login {
  background-color: #e8f0fe !important;
  border: none;
  padding: 10px 15px;
  width: 100%;
}

.inputForm-password {
  background-color: #ffff !important;
  border: none;
  padding: 5px 15px !important;
  width: 100% !important;
  border-width: 1px;
  border-style: solid;
  border-color: #d9d9d9;
  border-radius: 6px;
  transition: all 0.2s;
}

.container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: absolute;
  left: 10%;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.container.right-panel-active .sign-in-container {
  transform: translateX(100%);
}

.sign-up-container {
  left: 0;
  width: 50%;
  opacity: 0;
  z-index: 1;
}

.container.right-panel-active .sign-up-container {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.container.right-panel-active .overlay-container {
  transform: translateX(-100%);
}

.overlay {
  background: #e25c7c;
  background: -webkit-linear-gradient(to right, #ec4444, #f87896);
  background: linear-gradient(to right, #e94124, #fd7092);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay {
  transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-left {
  transform: translateX(-20%);
}

.container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.overlay-right {
  right: 0;
  transform: translateX(0);
}

.container.right-panel-active .overlay-right {
  transform: translateX(20%);
}

.social-container {
  margin: 20px 0;
}

.social-container a {
  border: 1px solid #dddddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

.ant-select-selector {
  width: 100%;
}

.form-login .ant-form-item {
  width: 200px;
}

.mar-10 {
  margin-bottom: 10px;
}
