* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.row {
  display: flex;
  align-items: center;
}

.col-row {
  flex-direction: column;
}

.mg-b-10 {
  margin-bottom: 10px;
}

.justify-center {
  justify-content: center;
}

.justify-left {
  justify-content: left;
}
.justify-right {
  justify-content: right;
  align-items: center;
}
.align-items-center {
  align-items: center;
}

.wrap {
  flex-wrap: 10px;
}

.line-dashed {
  border-style: dashed;
  border-width: 1px;
  border-color: rgba(18, 17, 17, 0.2);
}

.container-slider {
  width: 100%;
  overflow: hidden;
}

.slider {
  width: 100%;
  background-color: #f1f1f1;
  position: relative;
  right: -100%;
  animation: slide-in 1s forwards;
}

@keyframes slide-in {
  100% {
    right: 0;
  }
}

.post-image-hidden {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  max-height: 600px;
}

a:-webkit-any-link {
  cursor: pointer;
  text-decoration: none;
}

::-webkit-scrollbar {
  display: none;
}

.review-img {
  position: relative;
  margin: 8px;
}

.remove-img {
  position: absolute;
  top: 10px;
  right: 10px;
}

.remove-img i {
  color: rgb(255, 255, 255);
}

.remove-img i:hover {
  color: rgb(240, 65, 65);
}

.wrap {
  flex-wrap: wrap;
}

.handle-img{
  position: relative;
}