.create-package-btn {
}
.fa-square-plus {
  padding: 5px;
}
/* create form */
.package-form-create {
  background-color: var(--main-color);
  padding: 10px;
  border-radius: 10px;
}
.create-package-item {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.create-package-item input {
  margin-right: 17px;
  width: 170px;
  height: 30px;
}
.create-package-item label {
  color: #ffffff;
}
.descLabel {
  color: #ffffff;
}

.select-location {
  margin-left: 13px;
}
/* show package */
.package_Item {
  margin-top: 15px;
  display: flex;
  width: 300px;
  height: 233px;
  border: 3px solid var(--main-color);
  border-radius: 10px;
  padding: 0.5rem;
   transition: all 0.3s ease;
}

.package_Img img {
  width: 93px;
  height: 134px;
  padding: 5px;
  border-radius: 10px;
  object-fit: cover;
}
.package_Content {
  margin-left: 10px;
  position: relative;
  cursor: pointer;
}
.package_Item:hover {
  transform: scale(1.08);
}
.package_Content .package-desc-hide {
  position: absolute;
  font-size: 16px;
  left: 165px;
  background-color: var(--main-color);
  padding: 5px;
  display: none;
  width: 200px;
  border-radius: 10px;
  opacity: 0.9;
}
.package_Content:hover .package-desc-hide {
  display: block;
}

.package-desc {
  height: 30px;
  font-size: 12px;
  width: 170px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.package-title {
  height: 40px;
  font-size: 16px;
  font-weight: 700;
}
.package-price {
  color: red;
}
.package-userName {
  font-size: 14px;
}

.time-create-package {
  font-size: 13px;
}
.remove-btn {
  background: none;
  border: none;
}
/* package detail modal */

.ant-modal-wrap {
}
.package-item-detail-modal {
  display: flex;
}
.ant-modal-content {
  width: 900px;
  height: 500px;
}
.ant-modal-footer {
  display: none;
}
.package_Img-detail-modal img {
  width: 250px;
  height: 400px;
  object-fit: cover;
}
.package-content-detail-modal {
  margin-left: 20px;
}
.package-title-detail-modal {
  font-size: 30px;
}
.package-price-detail-modal {
  font-size: 25px;
  font-weight: 800;
  color: #eb1d36;
}
.package-desc-detail-modal {
  font-size: 20px;
  font-weight: 600;
}
.package-option-detail-modal {
  position: relative;
}
.booking-btn-detail-modal {
  position: absolute;
  left: 420px;
  top: 100px;
  width: 150px;
  border-radius: 10px;
  font-size: 16px;
  padding: 5px;
  font-weight: 900;
  background-color: #eb1d36;
  color: #ffffff;
  border: 2px solid #eb1d36;
}
.booking-btn-detail-modal:hover {
  color: #eb1d36;
  border: 2px solid #eb1d36;
  background-color: #ffffff;
}

