/* show package for couple */
.booking-btn {
  margin-top: 10px;
  margin-left: 90px;
  border-radius: 10px;
  font-size: 16px;
  padding: 5px;
  font-weight: 900;
  background-color: #eb1d36;
  color: #ffffff;
  border: 2px solid #eb1d36;
  cursor: pointer;
}
.booking-btn:hover {
  color: #eb1d36;
  border: 2px solid #eb1d36;
  background-color: #ffffff;
}
/* show package for photographer */
.photographer-option {
  border: none;
  background: none;
  padding: 2px;
  margin-top: 5px;
  margin-left: 140px;
  cursor: pointer;
}

/* Modal */

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.678);
}

.modal-content {
  padding: 20px;
  border-radius: 10px;
  width: 200px;
  height: 600px;
}

.close {
  color: #aaaaaa;
  float: right;
  font-size: 30px;
  font-weight: bold;
  margin-top: -270px;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.modal-form {
  width: 350px;
  align-items: center;
  padding: 10px;
  background-color: var(--main-color);

  border-radius: 5px;
}
.modal-form input {
}

.create_TitlePackage input {
  padding: 10px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.desc-input {
  padding: 10px;
  width: 90%;
  min-width: 250px;
  min-height: 60px;
  border: none;
  outline: none;
  resize: none;
  border-radius: 10px;
  margin-left: 13px;
  background-color: rgba(255, 255, 255, 0.523);
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.desc-input:hover {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}
.iconsPackage {
  display: flex;
  justify-content: space-around;
}
.iconsPackage label i {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: #f5f5f5;
  font-size: 1.2rem;
  transition: all 0.3s;
}
.iconsPackage label i:hover {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.chooseImg-package {
  display: none;
}

.title_Package {
  padding: 10px;
  width: 100%;

  border-radius: 5px;
  font-size: 16px;
}
.price_Package {
  padding: 10px;
  background-color: var(--main-color);
  border: 1px solid var(--main-color);
  border-radius: 5px;
  font-size: 16px;
  color: #ffffff;
  text-align: center;
}
.save-btn {
     border: none;
    font-weight: bold;
    background-color: #f5f5f5;
    color: #e39797;
    cursor: pointer;
    padding: 0px 10px;
    border-radius: 10px;
}

.save-btn:hover {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
@media only screen and (max-width: 1450px) {
  .create-package {
    margin-right: 20px;
  }
}
@media only screen and (max-width: 1100px) {
  .create-package {
  }
  .create-package h3 {
    font-size: 14px;
  }
}
@media only screen and (max-width: 768px) {
  .createPackageContainer {
    margin-top: 180px;
  }
}


